import request from '@/utils/request'
export function getArticles (params) {
  return request({
    url: 'http://ttapi.research.itcast.cn/app/v1_1/articles',
    params: {
      with_top: 0,
      ...params
    },
    method: 'get'
  })
}
export function disLikeArticle (data) {
  return request({
    url: '/article/dislikes',
    method: 'post',
    data
  })
}
// 举报文章的接口
export function report (data) {
  return request({
    url: '/article/reports',
    method: 'post',
    data
  })
}
export function allArticle () {
  return request({
    url: 'channels',
    method: 'get'
  })
}
// 联想搜索
export function suggestion (params) {
  return request({
    url: '/suggestion',
    params
  })
}
// 获取文章搜索的结果
export function searchArtile (params) {
  return request({
    url: '/search',
    params
  })
}
// 获取文章详情
export function getArticleInfo (id) {
  return request({
    url: `articles/${id}`,
    method: 'get'
  })
}
// 获取评论或者获取评论回复
export function getComments (params) {
  return request({
    url: 'comments',
    params
  })
}
// 对文章评论 或者对评论评论
export function commentOrReply (data) {
  return request({
    url: 'comments',
    method: 'post',
    data
  })
}
