<template>
  <div class="comment">
      <!-- 这里的目的是：用户进入文章详情就加载评论数据 -->
      <van-list @load="onLoad" v-model="loading" :finished="finished" finished-text="没有更多了">
          <div class="item van-hairline--bottom van-hairline--top" v-for="comment in comments" :key="comment.com_id.toString()">
              <van-image round width="1rem" height="1rem" fit="fill" src="https://img.yzcdn.cn/vant/cat.jpeg"></van-image>
              <div class="info">
                  <p>
                      <span class="name">{{comment.aut_name}}</span>
                      <span style="float:right">
                          <span class="van-icon van-icon-good-job-o zan"></span>
                          <span class="count">{{comment.like_count}}</span>
                      </span>
                  </p>
                  <p>{{comment.content}}</p>
                  <p>
                      <span class="time">{{comment.pubdate | relTime}}</span>&nbsp;
                      <van-tag plain @click="openReply(comment.com_id)">{{comment.reply_count}} 回复</van-tag>
                  </p>
              </div>
          </div>
      </van-list>
      <div class="reply-container van-hairline--top">
          <van-field v-model.trim="value" placeholder="写评论...">
            <van-loading  v-if="submiting" slot="button" type="spinner" size="16px"></van-loading>
            <span class="submit" @click="submit" v-else slot="button">提交</span>
          </van-field>
      </div>
      <van-action-sheet  @closed="reply.commentId=null" :round="false" v-model="showReply" title="回复评论" class="reply_dialog">
          <van-list
              v-model="reply.loading"
              :finished="reply.finished"
              finished-text="没有更多了"
              :immediate-check="false"
              @load="getReply"
            >
            <div class="item van-hairline--bottom van-hairline--top" v-for="comment in reply.list" :key="comment.com_id.toString()">
              <van-image
                round
                width="1rem"
                height="1rem"
                fit="fill"
                :src="comment.aut_photo"
              />
              <div class="info">
                <p><span class="name">{{comment.aut_name}}</span></p>
                <p>{{comment.content}}</p>
                <p><span class="time">{{comment.pubdate | relTime}}</span></p>
              </div>
            </div>
          </van-list>
          <div class="reply-container van-hairline--top">
          <van-field v-model.trim="value" placeholder="写评论...">
            <van-loading  v-if="submiting" slot="button" type="spinner" size="16px"></van-loading>
            <span class="submit" @click="submit" v-else slot="button">提交</span>
          </van-field>
      </div>
      </van-action-sheet>
  </div>
</template>

<script>
import { getComments, commentOrReply } from '@/api/articles'
export default {
  data () {
    return {
      loading: false,
      finished: false,
      value: '',
      submiting: false,
      comments: [],
      offset: null,
      showReply: false,
      reply: {
        commentId: null,
        loading: false,
        finished: false,
        list: [],
        offset: null
      }
    }
  },
  methods: {
    async onLoad () {
      // 加载评论数据
      let data = await getComments({
        type: 'a',
        offset: this.offset,
        source: this.$route.query.articleId
      })
      // console.log(data)
      this.comments.push(...data.results)
      this.finished = data.last_id === data.end_id
      if (!this.finished) {
        // 如果没有结束的话
        this.offset = data.last_id
        this.loading = false
      }
    },
    async openReply (commentId) {
      this.showReply = true
      // 把所有的值都给他设置成原始状态
      this.reply.commentId = commentId.toString()
      this.reply.list = []
      this.reply.offset = null
      this.reply.finished = false
      this.reply.loading = true
      this.getReply()
    },
    async getReply () {
      // console.log('进来了')
      let data = await getComments({
        type: 'c',
        source: this.reply.commentId,
        offset: this.reply.offset
      })
      // console.log(data)
      this.reply.list.push(...data.results)
      this.reply.loading = false
      this.reply.finished = data.last_id === data.end_id
      if (!this.reply.finished) {
        // 如果还有数据接着加载
        this.reply.offset = data.last_id
      }
    },
    async submit () {
      if (!this.value) return false// 如果为空就返回
      this.submiting = true
      await this.$sleep()
      try {
        let data = await commentOrReply({
          target: this.reply.commentId ? this.reply.commentId : this.$route.query.articleId,
          content: this.value,
          art_id: this.reply.commentId ? this.$route.query.articleId : null
        })
        // console.log(data)
        if (this.reply.commentId) {
          // 如果是给评论进行了评论
          this.reply.list.unshift(data.new_obj)
          // 要告诉找到文章对应的评论 改他的数量
          const comment = this.comments.find(item => item.com_id.toString() === this.reply.commentId.toString())
          comment.reply_count++
        } else {
          // 如果是给文章进行了评论
          this.comments.unshift(data.new_obj)
        }
        // 清空评论框
        this.value = ''
      } catch (error) {
        this.$gnotify({
          type: 'danger',
          message: '评论失败'
        })
      }
      this.submiting = false// 关闭提交状态
    }

  }

}
</script>

<style lang="less" scoped>
.comment {
  margin-top: 10px;
  /deep/ .item {
    display: flex;
    padding: 10px 0;
    .info {
      flex: 1;
      padding-left: 10px;
      .name{
        color:#069;
      }
      .zan{
        vertical-align:middle;
        padding-right:2px;
      }
      .count{
        vertical-align:middle;
        font-size:10px;
        color: #666;
      }
      .time{
        color: #666;
      }
      p {
        padding: 5px 0;
        margin: 0;
      }
    }
  }
  /deep/ .van-button:active::before {
    background: transparent;
  }
}
.reply-container {
  position: fixed;
  left: 0;
  bottom: 0;
  height: 44px;
  width: 100%;
  background: #f5f5f5;
  z-index: 9999;
  .submit {
    font-size: 12px;
    color: #3296fa;
  }
}
.reply_dialog{
  height:100%;
  max-height: 100%;
  display: flex;
  flex-direction: column;
  overflow: hidden;
   .van-action-sheet__header{
    background: #3296fa;
    color:#fff;
    .van-action-sheet__close{
      color:#fff;
    }
  }
  .van-action-sheet__content{
    flex: 1;
    overflow-y: auto;
    padding: 0 10px 44px;
  }
}
.reply_dialog {
  height: 100%;
  max-height: 100%;
  display: flex;
  overflow: hidden;
  flex-direction: column;
  .van-action-sheet__header {
    background: #3296fa;
    color: #fff;
    .van-icon-close {
      color: #fff;
    }
  }
  .van-action-sheet__content{
    flex: 1;
    overflow-y: auto;
    padding: 0 10px 44px;
  }
}
</style>
