<template>
   <div class='container'>
    <van-nav-bar fixed title="文章详情" left-arrow @click-left="$router.back()" />
    <div class="detail">
      <h3 class="title">{{article.title}}</h3>
      <div class="author">
        <van-image round width="1rem" height="1rem" fit="fill" :src="article.aut_photo" />
        <div class="text">
          <p class="name">{{article.aut_name}}</p>
          <p class="time">{{article.pubdate | relTime}}</p>
        </div>
        <van-button round size="small" type="info" @click="follow">{{article.is_followed?'已关注':'+关注'}}</van-button>
      </div>
      <div class="content">
        <p v-html="article.content"></p>
      </div>
      <div class="zan">
        <van-button round size="small" :class="{active:article.attitude===1}" plain icon="like-o">点赞</van-button>
        &nbsp;&nbsp;&nbsp;&nbsp;
        <van-button round size="small" plain icon="delete" :class="{active:article.attitude===0}">不喜欢</van-button>
      </div>
      <comment></comment>
    </div>
  </div>
</template>

<script>
import { getArticleInfo } from '@/api/articles.js'
import { followUser, unfollowUser } from '@/api/user.js'
import Comment from './components/comment'
export default {
  data () {
    return {
      article_id: null,
      article: {}
    }
  },
  components: {
    Comment
  },
  methods: {
    async getArticleInfo () {
      // 获取文章的详情
      const result = await getArticleInfo(this.article_id)
      // console.log(result)
      this.article = result
    },
    async follow () {
      try {
        // 关注或者取消关注
        if (this.article.is_followed) {
        // 说明已经关注了，需要取消关注
          await unfollowUser(this.article.aut_id.toString())
          // console.log(result)
          this.article.is_followed = false
        } else {
          await followUser(this.article.aut_id.toString())
          // console.log(result)
          this.article.is_followed = true
        }
        this.$gnotify({ type: 'success', message: '操作成功' })
      } catch (error) {
        // console.log(error)
        this.$gnotify({ type: 'danger', message: '操作失败' })
      }
    }

  },
  created () {
    // 获取文章的id
    this.article_id = this.$route.query.articleId
    this.getArticleInfo()
  }
}
</script>

<style lang="less" scoped>
.container {
  height: 100%;
  overflow-y: auto;
  box-sizing: border-box;
}
.detail {
  padding: 46px 10px 44px;
  .title {
    font-size: 18px;
    line-height: 2;
  }
  .zan{
    text-align: center;
    padding: 10px 0;
    .active{
      border-color:red;
      color: red;
    }
  }
  .author {
    padding: 10px 0;
    display: flex;
    position:sticky;
    background-color: #fff;
    top:46px;
    .text {
      flex: 1;
      padding-left: 10px;
      line-height: 1.5;
      .name {
        font-size: 14px;
        margin: 0;
      }
      .time {
        margin: 0;
        font-size: 12px;
        color: #999;
      }
    }
  }
  .content {
    padding: 20px 0;
    overflow: hidden;
    white-space: pre-wrap;
    word-break: break-all;
    /deep/ img{
      max-width:100%;
      background: #f9f9f9;
    }
    /deep/ code{
      white-space: pre-wrap;
    }
  }
}
</style>
